import uniSwipeAction from '@/components/uni-swipe-action/uni-swipe-action.vue'
import uniLoadMore from "@/components/uni-load-more/uni-load-more.vue";
let _this, timer
export default {
	components: {
		uniSwipeAction,
		uniLoadMore
	},
	data() {
		return {
			isDoRefresh:false,
			loadingType: 'more',
			message: [],
			options2: [{
				text: '删除',
				style: {
					backgroundColor: '#FF6000'
				}
			}],
			pageNum: 1
		};
	},
	onReachBottom: function() {
		if (timer != null) {
			clearTimeout(timer);
		}
		timer = setTimeout(function() {
			_this.getMoreMessage();
		}, 1000);
	},
	onPullDownRefresh: function() {

		this.getMessage();
	},
	onLoad: function() {
		_this = this
		this.getMessage()
	},
	onShow: function() {
		// this.getMessage()
		if (this.isDoRefresh){
			this.isDoRefresh = false
			this.getMessage()
		}
	},
	methods: {
		getMoreMessage: function() {
			if (_this.loadingType !== 'more') { //loadingType!=0;直接返回
				return false;
			}
			_this.loadingType = 'loading';
			uni.showNavigationBarLoading(); //显示加载动画
			let url = this.Api.noReadList
			let data = {
				pageNum: _this.pageNum,
				pageSize: 20
			}
			this.myRequest(data, url, 'post').then(res => {
				if (res.data.data.list.length == 0) { //没有数据
					console.log("no data")
					_this.loadingType = '';
					uni.hideNavigationBarLoading(); //关闭加载动画
					return;
				}
				_this.pageNum++; //每触底一次 page +1

				_this.message = _this.message.concat(res.data.data.list)
				_this.loadingType = 'more'; //将loadingType归0重置
				uni.hideNavigationBarLoading(); //关闭加载动画

			}).catch(err => {
				wx.showToast({
					title: err.data.errMsg,
					icon: 'none'
				});
			});
		},
		getMessage: function() {
			_this.pageNum = 1
			_this.loadingType = 'more';
			uni.showNavigationBarLoading();
			// uni.showLoading({
			// 	title: '加载中',
			// 	mask: true
			// })
			let url = this.Api.noReadList
			let data = {
				pageNum: _this.pageNum,
				pageSize: 20
			}
			this.myRequest(data, url, 'post').then(res => {
				console.log(res)
				if (res.data.status == 0) {
					// uni.hideLoading()
					_this.pageNum++;

					_this.message = res.data.data.list
				}

			}).catch(err => {
				wx.showToast({
					title: err.data.errMsg,
					icon: 'none'
				});
			});
			uni.hideNavigationBarLoading();
			uni.stopPullDownRefresh(); //得到数据后停止下拉刷新
		},
		bindClick(messageCode, e) {
			console.log(messageCode)
			console.log(e)
			if (e.index == 0) {
				let url = this.Api.delReadList
				let data = {
					messageCode: messageCode
				}
				this.myRequest(data, url, 'get').then(res => {
					console.log(res)
					if (res.data.status == 0) {
						uni.showToast({
							title: '删除成功',
							icon: 'none',
							duration: 1000
						})
						setTimeout(function() {
							_this.getMessage()
						}, 2000);

					}

				}).catch(err => {
					wx.showToast({
						title: err.data.errMsg,
						icon: 'none'
					});
				});
			}
		},
		getMessageDetail:function(messageCode){
			let url = this.Api.DetailReadList
			let data = {
				messageCode: messageCode
			}
			this.myRequest(data,url,'get').then(res => {
			   console.log(res)
			}).catch(err => {
			  wx.showToast({
			    title: err.data.errMsg,
			    icon: 'none'
			  });
			});
		},
		toDeail: function(item) {
			if (item.isRead == 0){
				if (plus.os.name.toLowerCase() == 'ios') {
					console.log("heerer")
					 var UIApplication = plus.ios.import("UIApplication");  
					 var app = UIApplication.sharedApplication();  
					 //获取应用图标的数量  
					 
					var oldNum = app.applicationIconBadgeNumber();  
					console.log("oldNUM",oldNum)
					if (oldNum >0){
						var newNum = oldNum - 1;
						
						 plus.runtime.setBadgeNumber(newNum);
						//导入个推原生类
						 var GeTuiSdk = plus.ios.importClass("GeTuiSdk");
						 GeTuiSdk.setBadge(newNum);
					}
				}
			}
			
			
			let identity = uni.getStorageSync('pupDefault')
			this.getMessageDetail(item.messageCode)
			this.isDoRefresh = true
			switch (identity){
				case 'SELL_DEPUTY':
				    if (item.messageType ==1){
				    	uni.navigateTo({
				    		url: `/pages/qing-f-c/inquiryManage/sellDeputy/inquiry-details?inquiryNumber=${item.orderNum}`,
				    		
				    	});
				    } else if (item.messageType == 2){
						uni.navigateTo({
							url:`/pages/qing-f-c/inquiryManage/sampleInquiry/sellDeputy/inquiry-details?inquiryNumber=${item.orderNum}`
						})
					}else if (item.messageType == 3){
						uni.navigateTo({
							url:`/pages/qing-f-c/inquiryManage/sellDeputy/inquiry-details?inquiryNumber=${item.orderNum}`
						})
					}
					else if (item.messageType == 4){
						uni.navigateTo({
							url:`/pages/qing-f-c/inquiryManage/sampleInquiry/sellDeputy/inquiry-details?inquiryNumber=${item.orderNum}`
						})
					}
				    break;
				case 'BUY_DEPUTY':
				    if (item.messageType ==1){
				    	uni.navigateTo({
				    		url: `/pages/qing-f-c/inquiryManage/inquiry-details?inquiryNumber=${item.orderNum}`,
				    		
				    	});
				    } else if (item.messageType ==2){
						uni.navigateTo({
							url: `/pages/qing-f-c/inquiryManage/sampleInquiry/buyDeputy/inquiry-details?quoteNumber=${item.orderNum}`,
							
						});
					}else if (item.messageType ==3){
						uni.navigateTo({
							url: `/pages/qing-f-c/inquiryManage/quotedPrice/quotedPrice?quoteNumber=${item.orderNum}`,
							
						});
					}else if (item.messageType ==4){
						uni.navigateTo({
							url: `/pages/qing-f-c/inquiryManage/sampleInquiry/buyDeputy/quotationDetails/quotationDetails?number=${item.orderNum}`,
							
						});
					}		
				    break;
				case 'SALES_DIRECTOR':
				    if (item.messageType ==1){
				    	uni.navigateTo({
				    		url: `/pages/qing-f-c/inquiryManage/sales_director/inquiry-details?inquiryNumber=${item.orderNum}`,
				    		
				    	});
				    } else if (item.messageType ==2){
				    	uni.navigateTo({
				    		url: `/pages/qing-f-c/inquiryManage/sampleInquiry/sales_director/inquiry-details?inquiryNumber=${item.orderNum}`,
				    		
				    	});
				    }else if (item.messageType ==3){
				    	uni.navigateTo({
				    		url: `/pages/qing-f-c/inquiryManage/sales_director/quotation-detail?quoteNumber=${item.orderNum}`,
				    		
				    	});
				    }else if (item.messageType ==4){
				    	uni.navigateTo({
				    		url: `/pages/qing-f-c/inquiryManage/sampleInquiry/sales_director/quotation-details?number=${item.orderNum}`,
				    		
				    	});
				    }else if (item.messageType == 5) {
						uni.navigateTo({
							url: `/pages/qing-f-c/inquiryManage/sales_director/quotation-detail?quoteNumber=${item.orderNum}`,
							
						});
					}		
				    
				    break;
				case 'REGIONAL_MANAGER':
				    this.getMessage()
				    break;
				case 'ANALYST':
				    this.getMessage()
				    break;
			}
               
			if (item.messageType ==6){
				uni.navigateTo({
					url: `/pages/qing-f-c/customPicture/followRecordDetail?recordCode=${item.orderNum}`,
					
				});
			}
			if (item.messageType == 7){
				uni.navigateTo({
					url: `/pages/qing-f-c/customPicture/followRecordDetailSeller?recordCode=${item.orderNum}`,
					
				});
			}
			if (item.messageType == 8){
				uni.navigateTo({
					url: `/pages/qing-f-c/followRecord/publicDialog?recordCode=${item.orderNum}&buyOrSell=1`,
					
				});
			}
			if (item.messageType == 9){
				uni.navigateTo({
					url: `/pages/qing-f-c/followRecord/publicDialog?recordCode=${item.orderNum}&buyOrSell=2`,
					
				});
			}
			
			
		}
	}
}
